/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background-color: #f8f9fa;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.card {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  padding: 32px;
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.title {
  font-size: 24px;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 8px;
  line-height: 1.3;
}

.subtitle {
  font-size: 14px;
  color: #666;
  margin-bottom: 24px;
  line-height: 1.5;
}

.input-wrapper {
  margin-bottom: 20px;
  position: relative;
  width: 100%;
}

.input {
  width: 100%;
  padding: 12px 16px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  transition: border 0.2s ease;
  font-family: inherit;
  text-align: center;
}

.input:focus {
  border-color: #1a1a1a;
  box-shadow: 0 0 0 2px rgba(26, 26, 26, 0.1);
}

.button {
  width: 100%;
  padding: 12px;
  font-size: 14px;
  font-weight: 500;
  background-color: #1a1a1a;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: inherit;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.button:hover {
  background-color: #333;
}

.button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  transform: none;
}

.result {
  margin-top: 20px;
  font-size: 14px;
  animation: fadeIn 0.3s ease;
}

.prediction {
  font-weight: 500;
  margin: 0;
  line-height: 1.5;
}

.confidence {
  color: #666;
  font-weight: normal;
  display: inline-block;
  margin-top: 4px;
}

.error {
  color: #ff4b4b;
  margin: 0;
  line-height: 1.5;
}

.footer {
  font-size: 12px;
  color: #999;
  margin-top: 24px;
  margin-bottom: 0;
  line-height: 1.5;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

.loading-spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}